*{
    font-family: inherit;

}


html, body {
    color: white;
    background-color: #21232a;
    font-family:  'Arial Narrow Bold', sans-serif;
    font-size: 1rem;
    padding: 30px;

};

button{

}

input{
    color: grey; 
    font-size: 1.2rem;
    width: 100%;

}

.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

}


.card {
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;

}

.card p {
    padding: 5px;
    text-align: center;
}

.card img {
    max-height: 160px;
}
